@media (min-width: 1025px) {

    .bender {
        position: absolute;
        bottom: 0;
        z-index: -1;
      }    
  }

  @media (min-width: 320px) and (max-width: 480px) {  
    .bender {
        position: absolute;
        bottom: 0;
        z-index: -1;
        width: 70%;
      }    
  }