#base1 {    
    height: 9em;
    width: 6em;
    border: 0.1em solid black;
    border-radius: 15%;
    background:floralwhite; 
}

#base2 {
    position: relative;
    top: 0.2em;
    left: 0.2em;
    height: 8.5em;
    width: 5.5em;
    border: 0.05em solid crimson;
    border-radius: 15%;
    background:floralwhite; 
}

#gradient {
    position: relative;
    top: 0.2em;
    left: 0.2em;
    height: 8.1em;
    width: 5.1em;
    border-radius: 15%;
    background: radial-gradient(ellipse at top, blue, transparent),
                radial-gradient(ellipse at bottom, crimson, transparent),
                radial-gradient(circle at left, green, yellow);
}

.flip-card {
    background-color: transparent;
    width: 6em;
    height: 9em;
    perspective: 1000px;
  }
  
  .flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    border-radius: 15%;;
    box-shadow: 0 1em 1em 0 rgba(0,0,0,0.2);
  }
  
  .flip-card:hover .flip-card-inner {
    transform: rotateY(180deg);
  }
  
  .flip-card-front, .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
  }
  
  .flip-card-front {    
    color: black;
  }
  
  .flip-card-back {     
    transform: rotateY(180deg);
  }
