.klub1 {
    position: relative;
    top: 4em;
    left: 1.5em;
    height: 0em;
    width: 0em;    
    border-left: 1.5em solid transparent;
    border-right: 1.5em solid transparent;
    border-bottom: 2.5em solid green;    
}

.klub2 {
    position: relative;
    top: 0em;
    left: 0.3em;
    height: 2.6em;
    width: 2.6em;
    background: floralwhite;
    border-radius: 50% 50% 50% 100% ;    
}

.klub3 {
    position: relative;
    top: 0em;
    left: -3.2em;
    height: 2.6em;
    width: 2.6em;
    background: floralwhite;
    border-radius: 50% 50% 100% 50% ;    
}

.klub4 {
    position: relative;
    top: 0em;
    left: 3em;
    height: 2em;
    width: 2em;
    background: green;
    border-radius: 50%;
}

.klub5 {
    position: relative;
    top: 0em;
    left: -2.2em;
    height: 2em;
    width: 2em;
    background: green;
    border-radius: 50%;
}

.klub6 {
    position: relative;
    top: -1.5em;
    left: 1.1em;
    height: 2em;
    width: 2em;
    background: green;
    border-radius: 50%;
}

.klubrang1 {
    position: relative;
    top: -1.5em;
    left: -1.2em;
    font-size: x-large;
    font-style: oblique;    
}

.klubrang2 {
    position: relative;
    top: 1.9em;
    left: 1.2em;
    font-size: x-large;
    font-style: oblique;
    transform: rotate(180deg);
}