.diamond1 {
    position: relative;
    top: 2em;
    left: 1.5em;
    height: 0;
    width: 0;
    border-left: 1.5em solid transparent;
    border-right: 1.5em solid transparent;
    border-bottom: 2.5em solid blue;
}

.diamond2 {
    position: relative;
    top: 2.5em;
    left: -1.5em;
    height: 0;
    width: 0;
    border-left: 1.5em solid transparent;
    border-right: 1.5em solid transparent;
    border-top: 2.5em solid blue;
}

.diamondrang1 {
    position: relative;
    top: 0em;
    left: 1.9em;
    font-size: x-large;
    font-style: oblique;
    transform: rotate(180deg);
}

.diamondrang2 {
    position: relative;
    top: -5.6em;
    left: -1.9em;
    font-size: x-large;
    font-style: oblique;    
}
